import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NYPageComponent } from './newyear/newyear.component';

const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    redirectTo: 'newyear2018'
  },
  {
    path: 'newyear2018',
    component: NYPageComponent
  },
  {
    path: '**',
    pathMatch: 'full',
    redirectTo: ''
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class AppRoutingModule { }
