import { NgModule, ModuleWithProviders } from '@angular/core';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { SettingsService } from './settings.service';

interface ForRootOptions {
  colorScheme: string;
}

const FALLBACK_TRANSLATION_KEY = 'en';

@NgModule({
  imports: [
    TranslateModule
  ],
  exports: [
    TranslateModule
  ]
})
export class SharedModule {
  constructor(
    private _t: TranslateService
  ) {
    this._t.setDefaultLang(FALLBACK_TRANSLATION_KEY);
    this._t.use(this._t.getBrowserLang());
  }

  static forRoot(options?: ForRootOptions): ModuleWithProviders {
    return {
      ngModule: SharedModule,
      providers: [
        TranslateModule.forRoot().providers,
        SettingsService
      ]
    };
  }
}
