import { Component, OnInit, ElementRef, Renderer2, HostListener, OnDestroy } from '@angular/core';
import * as P from 'pixi.js';
import { Subject, BehaviorSubject, Observable, forkJoin } from 'rxjs';
import { filter, mapTo, take, takeUntil, startWith, debounceTime } from 'rxjs/operators';
import { isTrue } from 'src/utils/rxjs';

const T_CHRISTMASTREE = 'assets/sprites/christmastree.png';

interface Size {
  width: number;
  height: number;
}

@Component({
  selector: 'vk-nyscene-pixi',
  templateUrl: './pixi.component.html',
  styleUrls: [
    './pixi.component.css'
  ]
})
export class PixiComponent implements OnInit, OnDestroy {
  public loaderProgress$: Subject<number> = new Subject;

  private _destroy$: Subject<void> = new Subject;

  private _domReady$ = new BehaviorSubject(false);

  private _pixiApp: P.Application = new P.Application({
    transparent: true
  });

  private _resize$: Subject<Size> = new Subject;

  private _texturesReady$ = new BehaviorSubject(false);

  constructor(
    private _hostRef: ElementRef,
    private _renderer: Renderer2
  ) {
    this._loadTextures([
      T_CHRISTMASTREE
    ]);

    forkJoin(
      this._domReady$.pipe(isTrue(), take(1)),
      this._texturesReady$.pipe(isTrue(), take(1))
    ).pipe(
      take(1),
      takeUntil(this._destroy$)
    ).subscribe(() => this._setup());
  }

  ngOnDestroy() {
    this._destroy$.next();
    this._destroy$.complete();
  }

  ngOnInit() {
    this._renderer.appendChild(this._hostRef.nativeElement, this._pixiApp.view);
    this._onResize();
    this._domReady$.next(true);

    this._texturesReady$.pipe(
      filter(ready => ready === true),
      take(1)
    ).subscribe(() => this._setup());
  }

  private _getContainerSize(): Size {
    const el: HTMLElement = this._hostRef.nativeElement;
    return {
      width: el.offsetWidth,
      height: el.offsetHeight
    };
  }

  private _getTexture(key: string): P.Texture {
    return P.loader.resources[key].texture;
  }

  private _loadTextures(...loaderParams: any[]): Observable<void> {
    this._texturesReady$.next(false);
    P.loader
      .add(...loaderParams)
      .on('progress', (loader, resource) => this.loaderProgress$.next(loader.progress))
      .load(() => this._texturesReady$.next(true));
      return this._texturesReady$.pipe(
        filter(ready => ready === true),
        mapTo(undefined),
        take(1)
      );
  }

  @HostListener('window:resize', ['$event'])
  private _onResize(): void {
    const size = this._getContainerSize();
    this._pixiApp.renderer.resize(size.width, size.height);
    this._resize$.next(size);
  }

  private _setup() {
    const spriteChristmasTree = new P.Sprite(this._getTexture(T_CHRISTMASTREE));
    spriteChristmasTree.anchor.x = 0.5;
    spriteChristmasTree.anchor.y = 0.5;

    this._resize$.pipe(
      debounceTime(20),
      startWith(this._getContainerSize()),
      takeUntil(this._destroy$)
    ).subscribe((size: Size) => {
      const proportions = spriteChristmasTree.height / spriteChristmasTree.width;

      spriteChristmasTree.position = new P.Point(size.width / 2, size.height / 2);
      spriteChristmasTree.height = size.height;
      spriteChristmasTree.width = Math.round(size.height / proportions);
    });

    this._pixiApp.stage.addChild(spriteChristmasTree);
  }
}
