import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { symbol, symbolTriangle } from 'd3';

@Component({
  selector: 'vk-nytree-d3',
  templateUrl: './nytree-d3.component.html',
  styleUrls: ['./nytree-d3.component.scss']
})
export class NytreeD3Component implements OnInit {
  public treeHeight = 100;
  public treeWidth = 100;

  @ViewChild('tree') private _tree: ElementRef;

  constructor() { }

  ngOnInit() {
    console.log(this._tree.nativeElement);
    symbol().type(() => symbolTriangle).size(() => 100).context(this._tree.nativeElement);
  }

}
